.body_create_dish {
    display: grid;

}

.container_up {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.div1 {
    grid-area: 1 / 1 / 2 / 2;
}

.div2 {
    grid-area: 2 / 1 / 3 / 2;
}

.div3 {
    grid-area: 3 / 1 / 4 / 2;
}

.div4 {
    grid-area: 4 / 1 / 5 / 2;
}

.div5 {
    grid-area: 5 / 1 / 6 / 2;
}

.div6 {
    grid-area: 1 / 2 / 2 / 3;
}

.div7 {
    grid-area: 2 / 2 / 3 / 4;
}

.div8 {
    grid-area: 3 / 2 / 4 / 3;
}

.div9 {
    grid-area: 3 / 3 / 4 / 4;
}

.div10 {
    grid-area: 4 / 2 / 5 / 3;
}

.div11 {
    grid-area: 5 / 2 / 6 / 3;
}

.div12 {
    grid-area: 1 / 4 / 2 / 5;
}

.div13 {
    grid-area: 2 / 4 / 3 / 5;
}

.div14 {
    grid-area: 3 / 4 / 4 / 5;
}

.div15 {
    grid-area: 1 / 5 / 2 / 6;
}

.div16 {
    grid-area: 2 / 5 / 3 / 6;
}

.div17 {
    grid-area: 3 / 5 / 4 / 6;
}

.div18 {
    grid-area: 5 / 5 / 6 / 6;
}